import React, { useContext } from "react";
import { VscFileCode } from "react-icons/vsc";
import MainContext from "../../context/MainContext";
import {
  AboutDesc,
  AboutInfo,
  AboutItem,
  AboutTitle,
  Reviews,
  ReviewSlider,
  ReviewTitle,
  ServiceDesc,
  ServiceIcon,
  Services,
  ServicesTitle,
  ServiceTitle,
  SliderBox,
} from "./About.element";
import Slider from "react-slick";

const About = () => {
  const { userInfo } = useContext(MainContext);
  const { about, age, email, phone, residence, services, testimonials } = userInfo;

  console.log(testimonials);


  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          arrows: false
        },
      },
    ],
  };
  

  return (
    <div className="about position-absolute end-0 light-dark-bg col-lg-8 col-sm-10 col-10 m-0">
      <div className="container">
        <div className="container">
          <div className="row">
            <AboutTitle>Hakkımda</AboutTitle>
            <div className="col-lg-6">
              <AboutDesc>{about}</AboutDesc>
            </div>
            <div className="col-lg-6">
              <AboutInfo>
                <AboutItem>
                  <span>Yaş: </span> {age}
                </AboutItem>
                <AboutItem>
                  <span>Ülke: </span> {residence}
                </AboutItem>
                <AboutItem>
                  <span>E-mail: </span> {email}
                </AboutItem>
                <AboutItem>
                  <span>Telefon: </span> {phone}
                </AboutItem>
              </AboutInfo>
            </div>
          </div>
        </div>

        <Services>
          <div className="container">
            <ServicesTitle>Ne Yapıyorum</ServicesTitle>
            <div className="row">
              {services.map((service) => (
                <div key={service.id} className="col-lg-6">
                  <ServiceIcon>
                    <VscFileCode />{" "}
                  </ServiceIcon>
                  <ServiceTitle>{service.serviceTitle}</ServiceTitle>
                  <ServiceDesc>{service.serviceDescription}</ServiceDesc>
                </div>
              ))}
            </div>
          </div>
        </Services>

        <Reviews>
            <div className="container-md">
                <ReviewTitle>Ne Dediler</ReviewTitle>
                <ReviewSlider>
                    <Slider {...settings}>
                      {
                        testimonials.map(client => (
                          <SliderBox key={client.id}>
                              <img className="mx-auto" src={client.image} alt="profile-picture" />
                              <h3>{client.client}</h3>
                              <p>{client.message}</p>
                          </SliderBox>
                        ))
                      }
                    </Slider>
                </ReviewSlider>
            </div>
        </Reviews>
      </div>
    </div>
  );
};

export default About;
